import getAuthenticationDetails from './helpers/getAuthenticationDetails';
import getCognitoUser from './helpers/getCognitoUser';
import getUserPool from './helpers/getUserPool';
import getCognitoUserAttributes from './helpers/getCognitoUserAttributes';

export function auth(username, password, userPoolId, clientId) {
  const authenticationDetails = getAuthenticationDetails(username, password);
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);

  return new Promise((resolve, reject) => {
    cognitoUser.authenticateUser(authenticationDetails, {
      onSuccess(result) {
        resolve(result);
      },
      onFailure(err) {
        reject(err);
      },

    });
  });
}

export function create(username, password, userPoolId, clientId, attributes) {
  const userPool = getUserPool(userPoolId, clientId);
  const cognitoUserAttributes = getCognitoUserAttributes(attributes);

  return new Promise((resolve, reject) => {
    userPool.signUp(username, password, cognitoUserAttributes, null, (err, result) => {
      if (err) {
        return reject(err);
      }

      return resolve(result);
    });
  });
}

export function confirmRegistration(username, confirmationCode, userPoolId, clientId) {
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);

  return new Promise((resolve, reject) => {
    cognitoUser.confirmRegistration(confirmationCode, true, (err, result) => {
      if (err) {
        return reject(err);
      }

      return resolve(result);
    });
  });
}

export function resendConfirmationCode(username, userPoolId, clientId) {
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);

  return new Promise((resolve, reject) => {
    cognitoUser.resendConfirmationCode((err, result) => {
      if (err) {
        return reject(err);
      }

      return resolve(result);
    });
  });
}

export function getAttributes(username, userPoolId, clientId) {
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);

  return new Promise((resolve, reject) => {
    cognitoUser.getAttributes((err, result) => {
      if (err) {
        return reject(err);
      }

      return resolve(result);
    });
  });
}

export function deleteAttributes(username, attributeNames, userPoolId, clientId) {
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);

  return new Promise((resolve, reject) => {
    cognitoUser.deleteAttributes(attributeNames, (err, result) => {
      if (err) {
        return reject(err);
      }

      return resolve(result);
    });
  });
}

export function updateAttributes(username, attributes, userPoolId, clientId) {
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);
  const cognitoUserAttributes = getCognitoUserAttributes(attributes);

  return new Promise((resolve, reject) => {
    cognitoUser.updateAttributes(cognitoUserAttributes, (err, result) => {
      if (err) {
        return reject(err);
      }

      return resolve(result);
    });
  });
}

export function changePassword(username, oldPassword, newPassword, userPoolId, clientId) {
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);

  return new Promise((resolve, reject) => {
    cognitoUser.changePassword(oldPassword, newPassword, (err, result) => {
      if (err) {
        return reject(err);
      }

      return resolve(result);
    });
  });
}

export function forgotPassword(username, userPoolId, clientId) {
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);

  return new Promise((resolve, reject) => {
    cognitoUser.forgotPassword({
      onSuccess(data) {
        resolve(data);
      },
      onFailure(err) {
        reject(err);
      },
    });
  });
}

export function remove(username, userPoolId, clientId) {
  const cognitoUser = getCognitoUser(username, userPoolId, clientId);

  return new Promise((resolve, reject) => {
    cognitoUser.deleteUser((err, result) => {
      if (err) {
        return reject(err);
      }

      return resolve(result);
    });
  });
}



