import { CognitoUser } from 'amazon-cognito-identity-js';
import getUserPool from './getUserPool';

export default function getCognitoUser(username, userPoolId, clientId) {
  const userPool = getUserPool(userPoolId, clientId);

  const userData = {
    Username: username,
    Pool: userPool,
  };

  return new CognitoUser(userData);
}



