import { CognitoUserPool } from 'amazon-cognito-identity-js';

export default function getUserPoolHelper(userPoolId, clientId) {
  const poolData = {
    UserPoolId: userPoolId,
    ClientId: clientId,
  };

  return new CognitoUserPool(poolData);
}



